@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
@import url(https://fonts.googleapis.com/earlyaccess/nanummyeongjo.css);
@import url(https://fonts.googleapis.com/css?family=Lobster);
@import url(https://fonts.googleapis.com/earlyaccess/notosanskr.css);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.nav-button {
  font-family: 'Noto Sans KR', sans-serif;
}

.btn-kakao {
  background-color: #ffee33;
  border-color: #ffee33;
  color: black;
}

.btn-naver {
  background-color: #1ec545;
  border-color: #1ec545;
  color: white;
}

.profile-image {
  width: 90%;
}

.app {
  text-align: center;
  font-family: 'Nanum Myeongjo';
  /*font-family: 'Noto Sans KR', sans-serif;*/
  word-wrap: break-word;
  word-break: keep-all;
}

.app-header {
  background-color: #222;
  height: 80px;
  color: white;
  font-family: 'Lobster'
}

.app-footer {
  background-color: #f5f5f5;
  height: 50px;
  color: black;
  font-family: 'Noto Sans KR', sans-serif;
}

.app-fluid {
  padding-left: 0;
  padding-right: 0;
}

.Countdown {
    display: flex;
    font-family: sans-serif;
    text-align: center;
    padding-top: 40px;
    padding-left: 30px;
    padding-right: 30px;
    justify-content: center;
    align-items: center;
    resize: horizontal;
}

.time {
    font-size: 25px;
}

.login {
    font-family: sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
    height: 100vh;
    font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
    background-color: #f8f9fd;
}

input[type="text"],
input[type="password"] {
    height: 25px;
    border: 1px solid rgba(0, 0, 0, 0.2);
}

input[type="submit"] {
    margin-top: 10px;
    cursor: pointer;
    font-size: 15px;
    background: #01d28e;
    border: 1px solid #01d28e;
    color: #fff;
    padding: 10px 20px;
}

input[type="submit"]:hover {
    background: #6cf0c2;
}

.button-container {
    display: flex;
    justify-content: center;
}

.login-form {
    background-color: white;
    padding: 2rem;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.list-container {
    display: flex;
}

.error {
    color: red;
    font-size: 16px;
}


.input-container {
    display: flex;
    flex-direction: column;
    grid-gap: 8px;
    gap: 8px;
    margin: 10px;
}
